import React from "react";
import s from './Nav.module.css'
import {Link} from "react-scroll";


type PropsType = {
    toggle: boolean
    setToggle: () => void
}


export const Nav = (props: PropsType) => {

    return (
            <nav className={props.toggle ? `${s.navContainer} ${s.active}` : s.navContainer}>
                <ul className={s.list}>
                    <li>
                        <Link className={s.navLink} smooth={true} duration={1000} onClick={props.setToggle} to="home">Home</Link>
                    </li>
                    <li>
                        <Link className={s.navLink} smooth={true} offset={-80} duration={1000} onClick={props.setToggle} to="skill">Skills</Link>
                    </li>
                    <li><Link className={s.navLink} smooth={true} offset={-78} duration={1000} onClick={props.setToggle} to="portfolio">Portfolio</Link></li>
                    <li><Link className={s.navLink} smooth={true} offset={-78} duration={1000} onClick={props.setToggle} to="contacts">Contacts</Link></li>
                </ul>
            </nav>


    )

}