import {useFormik} from "formik";
import React, {useState} from "react";
import emailjs from 'emailjs-com';
import s from "./Contacts.module.css";
import * as Yup from 'yup';
import {AttentionSeeker} from "react-awesome-reveal";

const validation = Yup.object().shape({
    Name: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required'),
    Message: Yup.string()
        .min(3, 'Too Short!')
        .max(500, 'Too Long!'),
    Email: Yup.string().email('Invalid email').required('Required'),
    Phone: Yup.string()
        .matches(/^((\+[1-9]{1,4}[ -]?)|(\([0-9]{2,3}\)[ -]?)|([0-9]{2,4})[ -]?)*?[0-9]{3,4}[ -]?[0-9]{3,4}$/, 'Phone number is not valid')
        .min(11, 'The phone must start with "+" . Min length 11 characters ))')
        .required('A phone number is required'),
});


export const SignupForm = () => {

    let [message, setMessage] = useState("")


    const sendEmail = (object: Record<string, unknown> | undefined) => {
        emailjs.send(process.env.REACT_APP_YOUR_SERVICE_ID as string, process.env.REACT_APP_YOUR_TEMPLATE_ID as string, object, 'user_VK9XE0RXkwODzuCXuEK85')
            .then((result) => {
                setMessage("Message sent");
                setTimeout(() => {
                    setMessage("")
                }, 5000)
            }, (error) => {
                setMessage(error.text);
                setTimeout(() => {
                    setMessage("")
                }, 2000)
            });
    };

    const formik = useFormik({
        initialValues: {
            Name: '',
            Email: '',
            Message: '',
            Phone: '',
        },
        validationSchema: validation,
        onSubmit: (values,) => {
            sendEmail(values)
            formik.resetForm()
        },
    });
    return (
        <div>
            <form onSubmit={formik.handleSubmit} className={s.form}>
                <div className={s.inputBlock}>
                    <div className={s.inputContainer}>
                        {formik.errors.Name && formik.touched.Name ? (
                            <div className={s.error}>{formik.errors.Name}</div>
                        ) : null}
                        <input required placeholder={"Your Name*"}
                               className={formik.errors.Name && formik.touched.Name ? s.errorInput : s.input}
                               id="Name" name="Name" onChange={formik.handleChange}
                               value={formik.values.Name}/>
                    </div>

                    <div className={s.inputContainer}>
                        {formik.errors.Phone && formik.touched.Phone ? (
                            <div className={s.error}>{formik.errors.Phone}</div>
                        ) : null}
                        <input required placeholder={"Phone*"}
                               type={"tel"}
                               maxLength={12}
                               minLength={11}
                               className={formik.errors.Phone && formik.touched.Phone ? s.errorInput : s.input}
                               id="Phone" name="Phone" onChange={formik.handleChange}
                               value={formik.values.Phone}/>
                    </div>

                    <div className={s.inputContainer}>
                        {formik.errors.Email && formik.touched.Email ? (
                            <div className={s.error}>{formik.errors.Email}</div>
                        ) : null}
                        <input required id="Email" name="Email" placeholder={"Your Email*"}
                               className={formik.errors.Email && formik.touched.Email ? s.errorInput : s.input}
                               onChange={formik.handleChange}
                               value={formik.values.Email}/>
                    </div>

                </div>
                <textarea
                    required
                    id="Message"
                    name="Message"
                    placeholder={"Your Message"} className={s.textarea}
                    onChange={formik.handleChange}
                    value={formik.values.Message}
                />
                <button type="submit" className={s.send}>Submit</button>
            </form>

            {message &&
            <AttentionSeeker effect={"flash"} duration={5000} >
                <div className={s.response}>{message}</div>
            </AttentionSeeker>
            }

        </div>
    );
};
