import React from "react";
import styleContainer from '../../common/style/Container.module.css'
import s from './MyProjects.module.scss'
import {Project} from "./project/Project";
import {Title} from "../../common/title/Title";
import SocialNetwork from '../../assets/image/SocialNetwork.jpg'
import Todo from '../../assets/image/TodoList.jpg'
import Flower from '../../assets/image/flower.jpg'
import Counter from '../../assets/image/Counter.jpg'
import {AttentionSeeker} from "react-awesome-reveal";

export const MyProjects = React.memo(() => {

        let counter = "https://motorhead-xxx.github.io/Counter/"
        let todoList = "https://motorhead-xxx.github.io/TodoList/"
        let socialNetwork = "https://motorhead-xxx.github.io/Social_Network"

        return (
            <div className={s.myProjectBlock} id={"portfolio"}>

                <div className={`${styleContainer.container} ${s.projectContainer}`}>
                    <AttentionSeeker effect={"headShake"} duration={2000}>
                        <Title text={"Projects"} color={"Black"}/>
                        <div className={s.myProjects}>
                            <Project link={socialNetwork} style={SocialNetwork} title={"The Social Network"}
                                     description={"Project implemented on: React, Type Script, Redux," +
                                     " RestAPI, Thunk, Material UI, Unit Tests."}/>
                            <Project link={todoList} style={Todo} title={"Todolist"}
                                     description={"Project implemented on: React,Type Script, Redux, RestAPI, Thunk, Material" +
                                     " UI," +
                                     " Unit Tests, StoryBook"}/>

                            <Project style={Flower} title={"Flower market"} description={"...in progress"}/>

                            <Project link={counter} style={Counter} title={"Counter"} description={"My first project with React, Redux,Material UI, Type Script."}/>
                        </div>
                    </AttentionSeeker>
                </div>
            </div>
        )
    }
)