import React from "react";
import s from './Skills.module.css'
import styleContainer from '../../common/style/Container.module.css'
import {Skill} from "./skil/Skil";
import {Title} from "../../common/title/Title";
import develop from "../../assets/image/development.png"
import foundation from "../../assets/image/foundation.png"
import testing from "../../assets/image/test.png"
import design from "../../assets/image/design.png"
import {AttentionSeeker} from "react-awesome-reveal";

export const Skills = React.memo(() => {
        return (
            <div className={s.skillsBlock} id={"skill"}>
                <AttentionSeeker effect={"pulse"} duration={2000}>
                    <div className={`${styleContainer.container} ${s.skillsContainer}`}>

                        <Title text={"Skills"} color={"white"}/>
                        <div className={s.skills}>
                            <Skill title={"Development"} description={"React, Redux, AXIOS"} Image={develop}/>
                            <Skill title={"Foundation"} description={"JavaScript, TypeScript, HTML5, CSS3"} Image={foundation}/>
                            <Skill title={"Testing"} description={"Unit Tests, SnapShot, Storybook"} Image={testing}/>
                            <Skill title={"Design"} description={"Material UI, Ant-Design"} Image={design}/>
                        </div>

                    </div>
                </AttentionSeeker>
            </div>
        )
    }
)
