import React from "react";
import s from './Main.module.scss'
import PhotoMe from '../../assets/image/Photo.jpg'
import Particles from "react-tsparticles";


const Options: any = {
    fpsLimit: 60,
    particles: {
        color: {
            value: "#00b074",
        },
        collisions: {
            enable: true,
        },
        move: {
            direction: "none",
            enable: true,
            outMode: "bounce",
            random: false,
            speed: 1,
            straight: false,
        },
        number: {
            density: {
                enable: true,
                value_area: 800,
            },
            value: 100,
        },
        opacity: {
            value: 0.3,
        },
        size: {
            random: true,
            value: 5,
        },
    },
    detectRetina: true,
    fullScreen: {enable: false}
}


export const Main = React.memo(() => {
        return (
            <div className={s.mainBlock} id={"home"}>

                <Particles className={s.particles} options={Options}/>

                <div className={s.container}>
                    <div className={s.text}>
                        <h5 className={s.heading}>Front-end developer</h5>
                        <h1 className={s.name}><span style={{color: '#00b074'}}>Isaev</span> Alexandr</h1>
                    </div>
                    <div className={s.photoContainer}>
                        <img className={s.photo} src={PhotoMe}/>
                    </div>
                </div>

            </div>
        )
    }
)