import React, {useState} from 'react';
import './App.css';
import {Header} from "./component/header/Header";
import {Main} from "./component/main/Main";
import {Skills} from "./component/skills/Skils";
import {MyProjects} from "./component/myproject/MyProjects";
import {Contacts} from "./component/contacts/Contacts";
import {Footer} from "./component/footer/Footer";
import s from "./appWrap.module.css"

export const App = React.memo(() => {
        const [toggle, setToggle] = useState<boolean>(false)

        const burgerClick = () => {
            setToggle(!toggle)
        }

        const toggleChange = () => {
            setToggle(false)
        }

        return (
            <div className="App">
                <Header toggle={toggle} callBack={burgerClick} setToggle={toggleChange}/>

                <div className={toggle ? `${s.appWrapper} ${s.active}` : s.appWrapper} onClick={toggleChange}>
                    <Main/>
                    <Skills/>
                    <MyProjects/>
                    <Contacts/>
                    <Footer/>
                </div>
            </div>
        );
    }
)



