import React from "react";
import s from './Skill.module.scss'

type SkillPropsType = {
    title: string
    description: string
    Image?: string
}

export const Skill = React.memo((props: SkillPropsType) => {
        return (
            <div className={s.skill}>
                <div>
                    <img className={s.icon} src={props.Image}/>
                </div>
                <h3 className={s.titleSkill}>{props.title}</h3>
                <p className={s.description}>
                    {props.description}
                </p>
            </div>
        )
    }
)