import React from "react";
import s from './Tytle.module.scss'

type PropsType = {
    text: string
    color?:string
}

export const Title = (props:PropsType) => {
    return (
        <div className={s.title}>
            <h2 style={{color:props.color}}>{props.text}</h2>
        </div>
    )
}